<template>
  <v-app>
    <PageTitle title="主辦單位" />
    <ProgressCircular v-if="!organizers"/>
    <div v-else v-for="organizer in organizers" :key="organizer.id">
      <SectionTitle v-if="organizer.gsx$權重.$t === '組別'" class="mx-8" :title="organizer.gsx$內文.$t" align="left" :textColor="color.text.blue" />
      <ContentText v-if="organizer.gsx$權重.$t === '名字'" class="mx-12" :content="organizer.gsx$內文.$t" align="left" :blod="true" :textColor="color.text.orange" />
      <ContentText v-if="organizer.gsx$權重.$t === '職責'" class="mx-16" :content="organizer.gsx$內文.$t" align="left" :blod="false" />
      <v-img v-if="organizer.gsx$權重.$t === '照片'" class="mx-16" :max-width="width" :src="require(`@/assets/organizer/${organizer.gsx$內文.$t}.png`)" />
    </div>
  </v-app>
</template>

<script>
import googleSheets from '@/libraries/googleSheets';
import color from '@/libraries/color';

export default {
  name: 'Organizer',

  data () {
    return {
      color: color,
      organizers: null
    };
  },

  beforeMount () {
    this.init();
  },

  computed: {
    width () {
      let width = '';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          width = '40%';
          break;
        case 'sm':
          width = '40%';
          break;
        case 'md':
          width = '20%';
          break;
        case 'lg':
          width = '20%';
          break;
        case 'xl':
          width = '20%';
          break;
      }

      return width;
    }
  },

  methods: {
    init: async function () {
      this.organizers = (await googleSheets.get('主辦單位'));
    }
  }
};
</script>
